input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

body {
  display: flex;
  margin: 0;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: lightslategrey;
}

.container {
  display: grid;
  width: 350px;
  background-color: rgb(7, 7, 7);
  border: 3px solid #000000;
}

#title {
  background-color: rgb(224, 229, 233);
  font-weight: bold;
  line-height: 50px;
  text-align: center;
  border: 1px solid #000000;
  font-size: 16px;
  font-weight: 700;
}

#display {
  background-color: rgb(22, 60, 92);
  color: white;
  line-height: 50px;
  text-align: right;
  padding-right: 16px;
  border: 1px solid #000000;
  font-size: 16px;
  font-weight: 700;
}

#button-area {
  display: grid;
  grid-template-rows: repeat(4, 50px);
  grid-template-columns: repeat(4, auto);
  border: 1px solid #000000;
}

button {
  background-color: rgb(224, 229, 233);
  border: 1px solid #000000;
  width: 100%;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
}

button:hover {
  background-color: rgb(212, 215, 218);
}

.equals {
  display: grid;
  grid-template-rows: repeat(1, 50px);
}
